@import '../../../../scss/abstracts/constants';

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: $color-audiocall-text-light;
}

.title {
  margin: 50px 0 20px;
  color: $color-audiocall-text-light;

  @media (max-width: 720px) {
    font-size: 2rem;
  }

  @media (max-width: 520px) {
    font-size: 1.4rem;
  }
}

.text {
  width: 80%;
  font-size: 1.8rem;
  text-align: center;
  @media (max-width: 720px) {
    font-size: 1rem;
  }
}

.form {
  display: flex;
  flex-direction: column;
  width: 40%;
  margin-top: 40px;
  padding: 10px;
  @media (max-width: 720px) {
    width: 70%;
  }
}
